.header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  // margin: 25px 0;

  .logo-container {
    height: 100%;
    width: 70px;
    display: flex;
    // padding: 25px;
  }

  .logo {
    align-self: center;
  }

  .options {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .option {
      padding: 10px 15px;
      cursor: pointer;

      transition: all 0.1s;

      &:hover {
        transform: translateY(-2px);
      }
    }
  }
}
